import React from 'react';

// Bootstrap components used on page
import Carousel from 'react-bootstrap/Carousel';

// Font awesome icons
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import { faExpand } from '@fortawesome/free-solid-svg-icons'

class Image extends React.Component {

    scrollToMainContent() {
        const mainContentOffset = document.getElementById('main-content').offsetTop;
        const timelineHeight = document.getElementById('timeline').offsetHeight;
        console.log(mainContentOffset);
        console.log(timelineHeight);
        window.scrollTo(0, mainContentOffset - timelineHeight);
    }

    render() {
        const { image, title, dates, feature, handleModalShow } = this.props;

        return <React.Fragment>
            <Carousel
                controls={false}
                interval={null}
                className="timeline-carousel"
                indicators={false}>
                <Carousel.Item id="timeline">

                    <div className="min-vh-70 bg-center hover-focus-opacity" onClick={handleModalShow} style={{ backgroundImage: `url("${image.src}")` }}>
                        <div className="img-expand img-expand-lg"><FontAwesomeIcon icon={faExpand} size="2x" /></div>
                    </div>

                    <div className={`carousel-caption ${feature ? "pb-5" : ""}`}>
                        {feature ?
                            <React.Fragment>
                                <h3>
                                    <strong>{dates}</strong>: <span>{title}</span>
                                </h3>
                                <a className="position-absolute bop text-white hover-focus-opacity" onClick={event => this.scrollToMainContent(event)}><FontAwesomeIcon icon={faChevronDown} size="2x" /></a>
                            </React.Fragment> :
                            <div>
                                <strong>{dates} </strong><br />
                                <span>{title}</span>
                            </div>
                        }
                    </div>
                </Carousel.Item>
            </Carousel>
        </React.Fragment>

    }
}

export default Image;