import React from 'react';
import Figure from 'react-bootstrap/Figure';

// Font awesome icons
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExpand } from '@fortawesome/free-solid-svg-icons'

// Lazy load images
import FadeIn from "react-lazyload-fadein";

// Animate on scroll
import ScrollAnimation from 'react-animate-on-scroll';

class Image extends React.Component {

  render() {
    const { content, handleModalShow } = this.props;

    return <ScrollAnimation animateIn={content.animateIn ? content.animateIn : ""} initiallyVisible={content.animateIn ? false : true}>
      <Figure>
        <div className="position-relative hover-focus-opacity" onClick={handleModalShow}>
          <div className="img-expand"><FontAwesomeIcon icon={faExpand} /></div>

          <FadeIn>
            {onload => (
              <Figure.Image
                alt=""
                src={content.src}
                onClick={handleModalShow}
                onLoad={onload}
              />
            )}
          </FadeIn>
        </div>
        <Figure.Caption>
          <strong>{content.caption}</strong><br />
          {content.copyright ? `${String.fromCharCode(169)} ${content.copyright}` : ""}
        </Figure.Caption>
      </Figure>
    </ScrollAnimation>
  }
}

export default Image;