// Import react
import React from 'react';

// Import assets
import logo from './logo.png';
import logo_fr from './logo-fr.png';

import './App.css';
import './scss/custom.scss';

// Import content
import Chapters from "./data/chapters.json";

// Import third party modules
import xss from 'xss';
import "animate.css/animate.min.css";

// Import components
import Image from './components/Image.js'
import Feature from './components/Feature.js'
import Home from './components/Home.js'

// Import Bootstrap components
import Carousel from 'react-bootstrap/Carousel';
import Nav from 'react-bootstrap/Nav';
import Modal from 'react-bootstrap/Modal';
import Tooltip from 'react-bootstrap/Tooltip';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Figure from 'react-bootstrap/Figure';

const LANGUAGE = "en";

class App extends React.Component {

  constructor(props) {
    super(props);

    // Bind so `this` can be used correctly within the functions
    this.handleSelectChapter = this.handleSelectChapter.bind(this);
    this.handleSelectImage = this.handleSelectImage.bind(this);
    this.handleModalShow = this.handleModalShow.bind(this);
    this.handleModalClose = this.handleModalClose.bind(this);
    this.getImages = this.getImages.bind(this);

    // Se initial states
    this.state = {
      images: [],
      imageIndex: 0,
      chapterDirection: null,
      chapters: [],
      chapter: null,
      modalShow: false
    };
  }

  // Get the chapters for the page on component load
  componentDidMount() {

    // Saves all chapters first
    this.setState({
      chapters: Chapters[LANGUAGE]
    }, () => {

      // Check which chapter matches the hash
      let index = 0;

      this.state.chapters.forEach((chapter, chapterIndex) => {
        if (chapter.dates.title === window.location.hash.slice(1)) {
          index = chapterIndex;
        }
      })

      // Set chapter
      this.setState({
        chapter: this.state.chapters[index || 0],
        chapterIndex: index || 0
      }, () => this.getImages())
    });
  }

  // Gives us control to make custom thumbnails
  handleSelectImage(selectedIndex, e) {
    this.setState({
      imageIndex: selectedIndex
    });
  }

  // Gives us control to make custom thumbnails
  handleSelectChapter(selectedIndex) {
    const direction = selectedIndex < this.state.chapterIndex ? "prev" : "next";

    this.setState({
      chapterIndex: selectedIndex,
      chapterDirection: direction
    });


    // Hide all tooltips on change chapter
    Object.keys(this.refs).forEach((ref) => {
      this.refs[ref].setState({
        show: false
      });
    })

    window.scrollTo(0, 0)

    // Set this so we have the correct context for `this`
    const self = this;

    // Update the content _after_ the animate to new chapter
    setTimeout(() => {
      self.setState({
        chapter: this.state.chapters[selectedIndex]
      }, () => {
        window.location.hash = `#${this.state.chapter.dates.title}`;
        this.getImages();
      });

    }, 1000)
  }

  // Close images modal
  handleModalClose() {
    this.setState({ modalShow: false });
  }

  // Show images modal
  handleModalShow(index) {
    this.setState({
      imageIndex: index,
      modalShow: true
    });
  }

  // Go and get all of the images to populate the modal with
  // Getting them from the given chapter
  getImages() {
    let imageCount = 0; // 0 is for the feature image
    let images = [];
    const { chapter } = this.state;

    // First image is the feature image
    if (chapter.image) {
      chapter.image.index = imageCount;
      imageCount = imageCount + 1;
      images.push(chapter.image);
    }

    // Only image types in the content
    (chapter.content || []).map((row, rowIndex) => {
      return (row.content || []).map((column, columnIndex) => {
        if (column.type === "image") {
          column.index = imageCount;
          imageCount = imageCount + 1;
          images.push(column);
        }
        return images
      })
    })

    this.setState({
      images: images
    })
  }

  render() {
    const { chapters, chapter, chapterIndex, images, imageIndex } = this.state;

    // Renders home page if there's no chapter hash
    if (!window.location.hash) {
      return <Home />
    }

    // This means there are is selected to loop over
    if (!chapter) { return null; }

    // If there we are in a chapter
    return (
      <div id="top" >
        <div className="container">

          <div className="header p-2">
            <a href="/"><img className="img-fluid logo" src={LANGUAGE === 'en' ? logo : logo_fr} alt="" /></a>
          </div>

          {/* <h1>1889-2019: Celebrating 130 years of the IPU</h1> */}

          {/* Add tabs that trigger the timeline and content to update */}
          <Nav variant="tabs" defaultActiveKey="/home">
            {chapters.map((chapter, index) => {
              return <Nav.Item key={index}>
                <a href={`#${chapter.dates.title}`} onClick={(e) => { this.handleSelectChapter(index) }} className={`nav-link ${chapter.dates.title === window.location.hash.slice(1) ? "active" : ""}`}>
                  {chapter.dates.title}
                </a>
              </Nav.Item>
            })}
          </Nav>
        </div>


        <div className="timeline-container bg-secondary sticky-top">
          <div className="container">
            {/* Carousel that shows timeline and emulates sliding to and from the chapters */}
            <Carousel
              controls={false}
              interval={null}
              activeIndex={chapterIndex}
              onSelect={this.handleSelectChapter}
              direction="right"
              className="timeline-carousel"
              indicators={false}
              id="timeline">
              {chapters.map((chapter, chapterIndex) => {
                return <Carousel.Item key={chapterIndex}>
                  <ul className="timeline list-inline d-flex justify-content-around mb-0">
                    {/* Loop over every potential year in the chapter and see if a milestoneExists */}
                    {Array.from({ length: (chapter.dates.end - chapter.dates.start) + 1 }, (x, i) => i + chapter.dates.start).map((milestone, milestoneIndex) => {

                      const milestoneExists = chapter.milestones[milestone] || false;

                      // Always place empty years, so the timeline spacing is correct
                      if (!milestoneExists && milestone !== chapter.dates.start && milestone !== chapter.dates.end) {
                        return <li key={milestone} className="list-inline-item timeline-item-year"></li>;
                      }

                      // This is to make sure only the first and last years have circles if the year doesn't have a milestone
                      if (!milestoneExists) {
                        return <li key={milestone} className="list-inline-item timeline-item timeline-item-empty"><span className="timeline-year">{milestone}</span></li>;
                      }

                      // At this point we know it's a milestone
                      return <React.Fragment key={milestone}>
                        <OverlayTrigger
                          placement="bottom"
                          ref={`${chapterIndex}-${milestoneIndex}`}
                          overlay={
                            <Tooltip id={`tooltip-bottom`}>
                              {milestoneExists.content.map((paragraph, index) => <p key={index} dangerouslySetInnerHTML={{ __html: xss(paragraph) }}></p>)}
                            </Tooltip>
                          }
                        >
                          <li className="list-inline-item timeline-item timeline-item-active"><span className="sr-only">Milestone found for </span><span className="timeline-year">{milestone}</span></li>
                        </OverlayTrigger>
                      </React.Fragment>
                    })}
                  </ul>
                </Carousel.Item>
              })}
            </Carousel>
          </div>
        </div>

        {/* Output feature area using carousel slide styles */}
        <Feature feature={true} title={chapter.title} dates={chapter.dates.title} image={chapter.image} handleModalShow={() => this.handleModalShow(0)} />


        {/* Output the content as rows and columns */}
        <div id="main-content">
          {
            (chapter.content || []).map((row, rowIndex) => {
              return (<section key={rowIndex} className={`section ${row.background ? `${row.background} py-5` : ''}`}>
                <div key={rowIndex} className="container">

                  {/* Output title */}
                  {row.title ? <h2>{row.title}</h2> : ""}

                  {/* Background sections don't need a divider */}
                  {/* {!row.background ? <hr /> : ""} */}

                  <div className="row" key={rowIndex}>

                    {/* Loop through content columns */}
                    {(row.content || []).map((column, columnIndex) => {
                      return <div key={columnIndex} className={`col-md-${column.columns} ${column.offset ? `offset-md-${column.offset}` : ""}`}>
                        <h2>{column.title}</h2>

                        {/* Check column type */}
                        {column.type === "image"
                          // Full width images are output in a different style
                          ? column.columns === 12
                            ? <Feature handleModalShow={() => this.handleModalShow(column.index)} image={column} title={column.copyright} dates={column.caption} /> // 12 column images are displayed as feature
                            : <Image content={column} handleModalShow={() => this.handleModalShow(column.index)} />
                          // If we're content, then loop through the content arrays from the JSON
                          : column.content.map((paragraph, index) => {
                            return <div dangerouslySetInnerHTML={{
                              __html: xss(paragraph, {
                                // Specifying whitelist of elements and attributes for content from JSON
                                whiteList: {
                                  "a": ["href", "target", "class", "id"],
                                  "p": []
                                }
                              })
                            }} key={index}></div>
                          })
                        }
                      </div>
                    })}
                  </div>
                </div>
              </section>)
            })
          }

          {/* Output the previous and next navigation */}
          <div className="bg-light py-5">
            <div className="container">
              <div className="d-flex justify-content-between">
                <div>{chapterIndex > 0 ? <button type="button" className="btn btn-primary" onClick={(e) => { this.handleSelectChapter(chapterIndex - 1) }}>&lt; {chapters[chapterIndex - 1].dates.title}</button> : ""}</div>
                <div>{chapterIndex < chapters.length - 1 ? <button type="button" className="btn btn-primary" onClick={(e) => { this.handleSelectChapter(chapterIndex + 1) }} >{chapters[chapterIndex + 1].dates.title} &gt;</button> : ""}</div>
              </div>
            </div>
          </div>

        </div>

        {/* Modal to show chapter images all in one place */}
        <Modal size="xl" show={this.state.modalShow} onHide={this.handleModalClose}>
          <Modal.Header closeButton>
          </Modal.Header>
          <Modal.Body>
            <Carousel
              indicators={false}
              interval={0}
              activeIndex={imageIndex}
              onSelect={this.handleSelectImage}
            >
              {(images || []).map((column, columnIndex) => {
                return <Carousel.Item key={columnIndex}>
                  <div className="justify-content-center">

                    <p>
                      <img
                        className="mx-auto d-block img-fluid max-vh-70"
                        src={column.src}
                        alt="First slide"
                      />
                    </p>

                    <hr />

                    <Figure.Caption className="text-center">
                      <strong>{column.caption} </strong><br />
                      <span>{column.copyright}</span>
                    </Figure.Caption>
                  </div>
                </Carousel.Item>
              })
              }
            </Carousel>
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}


export default App;
